.registration-container {
  max-width: 1000px;
  margin: 30px auto;
  background: #fff;
  padding: 25px 40px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

h2 {
  text-align: center;
  margin-bottom: 25px;
  font-size: 24px;
  color: #000;
  font-weight: 600;
}

.section-title {
  font-size: 18px;
  margin-top: 25px;
  text-decoration: underline;
  color: #000;
  font-weight: 600;
}

.section {
  display: flex;
  flex-wrap: wrap;
  gap: 25px 25px; /* adds vertical & horizontal spacing */
  margin-top: 15px;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
}

.form-group {
  flex: 1 1 calc(33.333% - 20px);
  display: flex;
  flex-direction: column;
  min-width: 250px;
}

.form-group label {
  font-weight: 600;
  color: #000; /* label color black */
  margin-bottom: 8px;
}

.form-group input,
.form-group select {
  padding: 8px 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.2s ease-in-out;
}

.form-group input:focus,
.form-group select:focus {
  border-color: #007bff;
  outline: none;
}

.required {
  color: red;
}

.form-actions {
  text-align: center;
  margin-top: 25px;
}

.btn-submit, .btn-reset {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 25px;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer;
  font-size: 15px;
}

.btn-reset {
  background-color: #6c757d;
}

.btn-submit:hover {
  background-color: #0056b3;
}

.btn-reset:hover {
  background-color: #555;
}

/* ðŸ“± Responsive for tablets and mobiles */
@media (max-width: 992px) {
  .form-group {
    flex: 1 1 calc(50% - 20px);
  }
}

@media (max-width: 600px) {
  .form-group {
    flex: 1 1 100%;
  }

  .registration-container {
    padding: 20px;
  }
}

.error-text {
  color: red;
  font-size: 0.8rem;
}

.required {
  color: red;
}
